/************* Font Families *************/

@import url('https://fonts.googleapis.com/css?family=Nunito:300,300i,900,900i|Walter+Turncoat');

$font-body: 'Nunito', sans-serif;
$font-deco: 'Walter Turncoat', cursive;

$font-size: 16px;

/************* Weights & Styles *************/

$font-light:        300;
$font-normal:       400;
$font-medium:       600;
$font-bold:         700;

$font-italic:       italic;

em {
  font-style: $font-italic;
}

strong {
  font-weight: $font-bold;
}

/************* Body Defaults *************/

body {
  line-height: 1;
  font-size: $font-size;
  font-family: $font-body;
  font-weight: $font-normal;
}

/************* Headings *************/

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  clear: both;
  display: block;
  text-rendering: optimizeLegibility;
  padding: 0;
  margin: 0;
  line-height: 1.25;
  font-family: $font-body;
}

h1, .h1 {
  font-size: 2.5em;
  margin-bottom: 0.5em;
  font-weight: $font-bold;

  span {
    display: block;
    font-weight: $font-normal;
  }
}

h2, .h2 {
  font-size: 1.75em;
  margin-bottom: 0.5em;
  font-weight: $font-medium;
}

h3, .h3 {
  font-size: 1.5em;
  margin-bottom: 2em;
  font-weight: $font-light;
}

h4, .h4 {
  font-size: 1.25em;
  line-height: 1.5em;
  margin-bottom: 1em;
}

h5, .h5 {
  font-size: 1em;
  margin-bottom: 1em;
  font-weight: $font-bold;
  text-transform: uppercase;
}

h6, .h6 {
  font-size: 0.75em;
  margin-bottom: 2em;
  font-weight: $font-bold;
  text-transform: uppercase;
  letter-spacing: 0.25em;
}

/************* Lists *************/

ul, ol {
  margin: 0 0 2em 2rem;
  padding: 0;

  li {
    margin-bottom: 0.25em;
  }
}

ul {
  list-style-type: disc;
}

ol {
  list-style-type: decimal;
}

/************* Paragraphs *************/

p {
  margin: 0;
  padding: 0 0 2em 0;
  line-height: 1.5em;
  word-break: break-word;
  word-wrap: break-word;
  hyphens: none;
}
