

@mixin grid(
  $alignx: flex-start,
  $aligny: flex-start,
  $direction: row,
  $wrapping: nowrap,
  $alignwrap: flex-start) {

    display: flex;
    flex-direction: $direction;
    flex-wrap: $wrapping;
    justify-content: $alignx;
    align-items: $aligny;
    align-content: $alignwrap;
}

@mixin grid-single(
  $align: auto,
  $order: 1,
  $grow: 0,
  $shrink: 1,
  $basis: auto) {

    align-self:$align;
    order: $order;
    flex: $grow $shrink $basis;
}

